<template>
  <div class="icons">
     <swiper
     class="icons-swiper"
     :options="swiperOption">
      <swiper-slide
      v-for="(page, index) of pages"
      :key="index"
      class="icons-slide">
        <div
        class="icons-item"
        v-for="icons of page"
        :key="icons.id">
          <img class="icons-img" :src="icons.imgUrl" alt="icons.desc">
          <p class="icons-desc ellipsis">{{icons.desc}}</p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
     </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination'
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>
<style lang="scss" scoped>
  .icons{
    width: 750px;
    .icons-swiper{
      border-bottom: 1px solid lightgrey;
      padding-bottom: 30px;
    }
    .icons-slide{
      display: flex;
      flex-wrap: wrap;
    }
    .icons-item{
      width: 25%;
      height: 187.5px;
      .icons-img{
        width:110px;
        height:110px;
        margin: 20px auto;
      }
      .icons-desc{
        text-align: center;
        color: black;
      }
    }
  }
</style>



